.footer {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  bottom: 0px;
  left: 0px;
  padding: 30px;
}

.footer__text {
  font-family: 'KodeMono', monospace;
  font-size: 1.5vmin;
  font-weight: 400;
  color: var(--light-text-color);
}

.footer__logo {
  width: 4vmax;
  height: 6vmax;
  mask-image: url('../../../assets/images/PNG/monoplay_logo.png');
  mask-size: contain;
  mask-repeat: no-repeat;
  background-color: var(--light-text-color);
}

@media screen and (max-width: 780px) {
  .footer__text {
    font-size: 10px;
  }
}
