.play-screen {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding: 1vmin;
}

.play-screen__menu-button {
  position: absolute;
  right: 2vmax;
  top: 2vmax;
  opacity: 1;
  transition: 0.3s ease-in-out;
}

.play-screen__menu-button.hide {
  opacity: 0;
}
