.solved__heading,
.solved__message {
  font-family: 'StalinistOne', sans-serif;
  font-size: 1.8vmax;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
  background: var(--background-gradient);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-stroke: 0.3vmax transparent;
  color: var(--light-text-color);
  letter-spacing: 3px;
}

.solved__message {
  font-size: 1.2vmax;
}

.buttons-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  gap: 2vmax;
}

.solved__button {
  width: 13vmax;
  padding: 0.8vmax;
}

.solved {
  gap: 4vmax;
  padding: 2vmax;
}
