.main {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 200vh;
  background-image: url('../../../assets/images/JPG/back-picture.jpg');
  background-size: cover;
  background-position: center;
  transition: 0.3s ease-in-out;
  user-select: none;
}

.main.hide-menu {
  transform: translateY(-50%);
}
