.menu__screen {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.title__container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  gap: 1vmax;
}

.title__logo {
  width: 8vmin;
  height: 8vmin;
  background-color: var(--logo-color);
  mask-image: url('../../../assets/images/SVG/logo.svg');
  mask-size: cover;
  -webkit-mask-image: url('../../../assets/images/SVG/logo.svg');
  -webkit-mask-size: cover;
}

.title__text {
  font-family: 'AmaticSCBold', sans-serif;
  font-size: 10vmin;
  font-weight: 900;
  text-transform: uppercase;
  text-wrap: nowrap;
  letter-spacing: 3px;
  color: var(--logo-color);
}
