.cell,
.clue-text {
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid {
  display: grid;
  grid-template: auto / auto repeat(var(--grid-size), var(--cell-size));
}

.grid,
.cell {
  border-bottom: 1px solid var(--grid-border-color);
  border-right: 1px solid var(--grid-border-color);
}

.cell {
  background: var(--cell-background-color);
  cursor: pointer;
}

.cell.checked {
  background: var(--cell-dark-color);
}

.cell.checked.flash {
  animation: flash 1s linear infinite;
}

.cell.marked {
  position: relative;
}

.cell.marked::before,
.cell.marked::after {
  content: '';
  position: absolute;
  width: calc(var(--cell-size) / 2);
  height: 2px;
  background: var(--marked-cell-color);
}

.cell.marked::before {
  transform: rotate(45deg);
}

.cell.marked::after {
  transform: rotate(-45deg);
}

.cell.row-clue,
.cell.col-clue,
.clue-text,
.zero-cell {
  cursor: auto;
}

.cell.row-clue,
.cell.col-clue {
  background-color: var(--clues-dark-color);
}

.cell.row-clue {
  justify-content: flex-end;
  border-left: 2px solid var(--grid-border-color);
  border-right: 2px solid var(--grid-border-color);
}

.cell.col-clue {
  align-items: flex-end;
  flex-wrap: wrap;
  border-top: 2px solid var(--grid-border-color);
  border-bottom: 2px solid var(--grid-border-color);
}

.cell.fifth-col {
  border-right: 2px solid var(--grid-border-color);
}

.cell.fifth-row {
  border-bottom: 2px solid var(--grid-border-color);
}

.clue-text {
  width: var(--cell-size);
  height: var(--cell-size);
  font-family: 'KodeMono', monospace;
  font-size: calc(var(--cell-size) / 2);
  font-weight: 900;
  color: var(--light-text-color);
  background-color: var(--clues-color);
}

.zero-cell {
  border-bottom: 2px solid var(--grid-border-color);
  border-right: 2px solid var(--grid-border-color);
  background: none;
}

@keyframes flash {
  0% {
    background: var(--cell-dark-color);
  }
  50% {
    background: var(--cell-light-color);
  }
  100% {
    background: var(--cell-dark-color);
  }
}

@media screen and (max-width: 768px) {
  :root {
    --cell-size: min(max(calc(60vmin / var(--grid-size)), 6.5vmin), 12vmin);
  }
}
