.button {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 3px solid;
  background: var(--button-background-gradient);
  border-image: var(--border-gradient);
  right: 0;
  top: 0;
  padding: 1vmax;
  cursor: pointer;
  overflow: hidden;
}

.button::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 400%;
  background-image: url('../../../assets/images/JPG/vertigo.jpg');
  background-position: center;
  background-size: 400%;
  opacity: 0;
  transition: 0.3s ease-in-out;
  animation: object-rotation 3s linear infinite;
}

.button:hover::before {
  opacity: 0.05;
}

.button__text {
  font-family: 'KodeMono', monospace;
  font-size: 1.5vmax;
  font-weight: 900;
  text-align: left;
  text-transform: uppercase;
  background: var(--button-background-gradient);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-stroke: calc((1vw + 1vh) / 8) transparent;
  color: var(--light-text-color);
  transition: 0.3s ease-in-out;
}

.button:hover > .button__text {
  transform: scale(98%);
  animation: object-scale 0.8s linear infinite;
}
