@font-face {
  font-family: 'StalinistOne';
  src: url('/src/assets/fonts/StalinistOne-Regular.woff2') format('woff2'),
    url('/src/assets/fonts/StalinistOne-Regular.woff') format('woff');
}

@font-face {
  font-family: 'AmaticSCBold';
  src: url('/src/assets/fonts/AmaticSC-Bold.woff2') format('woff2'),
    url('/src/assets/fonts/AmaticSC-Bold.woff') format('woff');
}

@font-face {
  font-family: 'KodeMono';
  src: url('/src/assets/fonts/KodeMono-VariableFont_wght.woff2') format('woff2'),
    url('/src/assets/fonts/KodeMono-VariableFont_wght.woff') format('woff');
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --grid-border-color: rgb(0, 0, 0);
  --clues-dark-color: rgba(29, 48, 90, 0.5);
  --clues-color: rgba(77, 100, 143, 0.6);
  --cell-dark-color: rgb(29, 48, 90);
  --cell-light-color: rgb(213, 132, 144);
  --cell-background-color: rgb(250, 245, 224);
  --marked-cell-color: rgb(213, 132, 144);
  --slider-background-color: rgba(250, 245, 224, 0.6);
  --light-text-color: hsl(0, 0%, 100%);
  --logo-color: rgb(215, 163, 171);

  --background-gradient: linear-gradient(
    135deg,
    rgba(0, 0, 30, 0.7) 0%,
    rgba(144, 95, 116, 0.7) 50%,
    rgba(0, 0, 30, 0.7) 100%
  );

  --border-gradient: linear-gradient(
      45deg,
      rgb(40, 50, 41),
      rgb(255, 206, 227),
      rgb(40, 50, 41),
      rgb(255, 206, 227)
    )
    1;

  --button-background-gradient: linear-gradient(
    135deg,
    rgba(0, 0, 30, 0.7) 0%,
    rgba(80, 80, 150, 0.7) 50%,
    rgba(0, 0, 30, 0.7) 100%
  );

  --grid-size: 3;
  --cell-size: min(max(calc(15vmax / var(--grid-size)), 2vmax), 3vmax);
}

.body {
  position: relative;
  display: flex;
  width: 100vw;
  height: 100vh;
  background-image: url('./assets/images/JPG/back-picture.jpg');
  background-size: cover;
  background-position: center;
  overflow: hidden;
}

@keyframes object-rotation {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes object-scale {
  0% {
    transform: scale(100%);
  }
  50% {
    transform: scale(95%);
  }
  100% {
    transform: scale(100%);
  }
}
